// @flow

import React from "react"
import PropTypes from "prop-types"
import Helmet from "react-helmet"
import { Provider } from "rebass"
import Theme from "components/atoms/Theme"
import "typeface-montserrat"

const title = "SMARTSHOP. Augmented reality solutions crafted by Polidea"
const description =
  "Boost your customers’ shopping experience, train employees, optimize sales and manage your store effectively thanks to SMARTSHOP AR SOLUTIONS!"
const url = "http://smartshop.polidea.com"
const image = `${url}/social-cover.png`
const keywords =
  "AR, Augmented Reality, Retail, Smart, Shopping, Indoor Navigation, Personalized Marketing"

const TemplateWrapper = ({ data, children }) => (
  <Provider theme={Theme}>
    <Helmet
      meta={[
        {
          name: "description",
          content: description,
        },
        { name: "keywords", content: keywords },
        {
          property: "og:title",
          content: title,
        },
        {
          property: "og:type",
          content: "website",
        },
        {
          property: "og:description",
          content: description,
        },
        {
          property: "og:url",
          content: url,
        },
        {
          property: "og:image",
          content: image,
        },
        {
          name: "twitter:card",
          content: "summary_large_image",
        },
        { name: "twitter:title", content: title },
        {
          name: "twitter:description",
          content: description,
        },
        {
          name: "twitter:image",
          content: image,
        },
      ]}>
      <title>{title}</title>
      <style>{"body {margin: 0;padding: 0;}"}</style>
      <link rel="icon" href={`${data.site.pathPrefix}/favicon.ico`} />
    </Helmet>
    {children()}
  </Provider>
)

TemplateWrapper.propTypes = {
  children: PropTypes.func,
}

export default TemplateWrapper

export const query = graphql`
  query PathPrefix {
    site {
      pathPrefix
    }
  }
`



